import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import IInstance from 'src/app/interfaces/instance';
import { UpdateComponent } from '../../update/update.component';
import { InstanceComponent } from '../../instance/instance.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import IProject from 'src/app/interfaces/project';
import { format } from 'date-fns';
import { fr } from 'date-fns/locale';


@Component({
  selector: 'app-instance-item',
  templateUrl: './instance-item.component.html',
  styleUrls: ['./instance-item.component.scss']
})
export class InstanceItemComponent implements OnInit {

  @Output() refreshProjects = new EventEmitter();
  @Output() refreshCalendar = new EventEmitter();

  @Input() instance: IInstance;
  @Input() project: IProject;

  constructor(    private modalService: NgbModal) { }

  ngOnInit(): void {
  }

  openInstance(instance: IInstance): void {
    window.open(instance.url, '_blank');
  }

  getLastUpdate() {
    // appel 150'00 fois le console log voir pk
    let date = this.instance.lastUpdate[0]?.dueDate
    console.log(date)
    if(date){
      return date
      // return format(this.instance.lastUpdate[0]?.dueDate, 'dd MMM yyyy', { locale: fr })
    }
    return "";


  }


  openMAJModal(project: IProject, instance: IInstance) {

    const modalRef = this.modalService.open(UpdateComponent, {
      size: 'xl',
      animation: true,
    })

    modalRef.componentInstance.instance = instance;
    modalRef.componentInstance.project = project;

    modalRef.dismissed.subscribe(() => {
      this.refreshCalendar.emit();
    })



  }

  editEmails(instance: IInstance) {

    const modalRef = this.modalService.open(InstanceComponent, {
      size: 'xl',
      animation: true,
    })

    modalRef.componentInstance.instance = instance;
    modalRef.componentInstance.mode = 'patch-mail';

    modalRef.dismissed.subscribe((reason:any) => {
      if(reason === 'ok'){
        this.refreshProjects.emit();
      }
    })
  }


}
