import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { format } from 'date-fns';
import { stringify } from 'querystring';
import { Observable, of } from 'rxjs';
import { ProjectxApi } from 'src/app/api/projectxApi';
import IInstance from 'src/app/interfaces/instance';
import IProject from 'src/app/interfaces/project';
import IServer from 'src/app/interfaces/server';
import { ProjectsService } from 'src/app/services/projects.service';

@Component({
  selector: 'app-instance',
  templateUrl: './instance.component.html',
  styleUrls: ['./instance.component.scss']
})
export class InstanceComponent implements OnInit {

  @Input()
  public mode: 'create' | 'patch' | 'patch-mail' | 'delete' = 'create';

  @Input()
  public instance!: IInstance;

  /** il faut absolument patcher les mail avec un autre endpoint ou le changement de server avec un autre endpoint */

  //  // Mode: patch, update alreadyExist
  //  public update: IUpdate;

  projects: IProject[] = [];

  // je dois crer des interfaces pour envoy au serveur instance et server qui ne sont pas les modles mais juste des dto d'envoie
  // instance: any = {}
  constructor(private fb: FormBuilder, private projectxApi: ProjectxApi,
    private activeModal: NgbModal, private projectsService: ProjectsService) { }

  @Input() public servers: IServer[] = [];

  ngOnInit(): void {
    console.log(this.instance)
    // faut que je fasse un autre modal avec un autre endpoint pour juste changer les mails d'une instance
    // on a pas besoin des projets quand on change que les mails.

    // je sui un peu fatigu faudrait refactor ici a

    if(this.mode != "patch-mail"){
      this.getProjects();
      if (this.instance?.id) {
      this.instanceForm.controls['key'].setValue(this.instance.project.key);
      }
    }
    if (this.instance?.id) {
      this.instanceForm.controls['target'].setValue(this.instance.target);
      this.instanceForm.controls['type'].setValue(this.instance.type);
      this.instanceForm.controls['erpProject'].setValue(this.instance.erpProject);
      this.instanceForm.controls['serverNumber'].setValue(this.instance.server.number);
      this.instanceForm.controls['emails'].setValue(this.instance.emails?.join(";"));
    }

  }

  public instanceForm = this.fb.group({
    target: [null, Validators.required],
    type: [null, Validators.required],
    erpProject: [null, Validators.required],
    serverNumber: [null, Validators.required],
    key: [null, Validators.required],
    emails: []
  });


  async getProjects(): Promise<void> {
    this.projectsService
      .getProjects(false)
      .subscribe((projects: IProject[]) => (this.projects = projects));
  }


  async onSubmit() {

    switch (this.mode) {
      case "create":
        await this.createInstance();
        break;

      case "patch":
        await this.patchInstance();
        break;
      case "patch-mail":
        await this.patchInstanceEmails();
        break;

      // case "delete":
      //   await this.deleteUpdate();
      //   break;

      default:
        break;
    }
  }

  async createInstance() {

    try {
      let instance = await this.projectxApi.postInstance([this.instanceForm.value])
      this.instanceOK();
    } catch (error) {
      this.handleError("Create error")
    }



  }

  async patchInstance() {

    let form = this.instanceForm.value;

    let data: any = {};

    data.emails = form.emails?.split(/[;,]+/)
    data.id = this.instance.id
    data.target = form.target
    data.type = form.type
    data.erpProject = form.erpProject
    data.serverNumber = form.serverNumber
    data.key = form.key

    try {
      let instance = await this.projectxApi.patchInstance(data)
      this.instanceOK();
    } catch (error) {
      this.handleError("Update error")
    }

  }

  async patchInstanceEmails() {

    let form = this.instanceForm.value;

    try {
      let instance = await this.projectxApi.patchInstanceEmails(this.instance, form.emails?.split(/[;,]+/))
      this.instanceOK();
    } catch (error) {
      this.handleError("Update error")
    }

  }

  async deleteInstance() {
    if (confirm(`tes-vous sur de voulour supprimer l'instance ${this.instance.target}`)) {
      await this.projectxApi.deleteInstance(this.instance)
      this.instanceOK();
    }

  }

  private instanceOK() {

    this.activeModal.dismissAll("ok")
    // refresh server list

  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
