import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { ProjectxApi } from 'src/app/api/projectxApi';
import IInstance from 'src/app/interfaces/instance';
import IServer from 'src/app/interfaces/server';
import { ArrayUtilsService } from 'src/app/services/array-utils.service';
import { ServersService } from 'src/app/services/servers.service';
import { InstanceComponent } from '../instance/instance.component';
import { NewServerComponent } from '../new-server/new-server.component';

@Component({
  selector: 'app-manager',
  templateUrl: './manager.component.html',
  styleUrls: ['./manager.component.scss'],
})
export class ManagerComponent implements OnInit {
  servers: IServer[] = [];
  selectedServer!: IServer;

  instances: IInstance[] = [];

  public searchText: any;

  constructor(
    private serversService: ServersService,
    private toastr: ToastrService,
    private modalService: NgbModal,
    private router: Router,
    private projectxApi: ProjectxApi,
    private arrayUtils: ArrayUtilsService
  ) {}

  ngOnInit(): void {
    this.getServers();
  }

  async getServers() {
    this.serversService
      .getServers()
      .subscribe(
        (servers: IServer[]) => (
          (this.servers = this.arrayUtils.sortAlpha(servers, 'number')),
          this.displayInstance(this.servers[0])
        )
      );
  }

  createInstance() {
    const modalRef = this.modalService.open(InstanceComponent, {
      size: 'xl',
      animation: true,
    });

    modalRef.componentInstance.servers = this.servers;

    modalRef.dismissed.subscribe(() => {
      this.getServers();
    });
  }

  editInstance(instance: IInstance) {
    const modalRef = this.modalService.open(InstanceComponent, {
      size: 'xl',
      animation: true,
    });

    modalRef.componentInstance.instance = instance;
    modalRef.componentInstance.mode = 'patch';
    modalRef.componentInstance.servers = this.servers;

    modalRef.dismissed.subscribe((reason) => {
      if (reason === 'ok') {
        this.getServers();
      }
    });
  }

  createServer() {
    const modalRef = this.modalService.open(NewServerComponent, {
      size: 'xl',
      animation: true,
    });

    modalRef.dismissed.subscribe((reason) => {
      if (reason === 'ok') {
        this.getServers();
      }
    });
  }

  async refreshProjects() {
    try {
      await this.projectxApi.getJiraProject();
      this.toastr.success('Refresh done');
    } catch (error) {
      this.toastr.error('Refresh error: ' + error);
    }
  }

  async getServersInstance() {
    try {
      let result = await this.projectxApi.getProjectsInstances();
      navigator.clipboard.writeText(JSON.stringify(result)).then(
        () => {
          this.toastr.success('check you clipboard');
        },
        () => {
          this.toastr.error('error');
        }
      );
    } catch (error) {
      this.toastr.error('Refresh error: ' + error);
    }
  }

  displayInstance(server: IServer) {
    this.selectedServer = server;
    this.instances = server.instances;
  }

  openUrl(instance: IInstance): void {
    window.open(instance.url, '_blank');
  }

  back() {
    this.router.navigate(['home']);
  }
}
