import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable, of } from 'rxjs';
import { ProjectxApi } from 'src/app/api/projectxApi';
import IServer from 'src/app/interfaces/server';

@Component({
  selector: 'app-new-server',
  templateUrl: './new-server.component.html',
  styleUrls: ['./new-server.component.scss']
})
export class NewServerComponent implements OnInit {

  server!: IServer;

  constructor(private fb: FormBuilder, private projectxApi: ProjectxApi, private activeModal: NgbModal) { }

  public serverForm = this.fb.group({
    number: ['', Validators.required],
    ip: ['', Validators.required],
    host: ['', Validators.required],
    port: ['', Validators.required],
    user: ['', Validators.required],
    password: ['', Validators.required]
  });

  ngOnInit(): void {
  }

  createServer() {
    this.server = this.serverForm.value;

    this.projectxApi.postServer(this.server).subscribe({
      next: this.serverOK.bind(this),
      error: this.handleError.bind(this),
    });


  }


  private serverOK(server: IServer) {

    this.activeModal.dismissAll("ok")
    // refresh server list

  }


  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
