import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import IPoject from "../interfaces/project";
import { ProjectxApi } from '../api/projectxApi';
@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  projects: IPoject[] = [];



  constructor(private api: ProjectxApi) { }


  addProject(project: IPoject) {
    this.projects.push(project);
  }

  /** GET project from the server */
  getProjects(onlyWithInstances:boolean): Observable<IPoject[]> {
    return this.api.getProjects(onlyWithInstances);
  }

  clearList() {
    this.projects = [];
    return this.projects;
  }
}
