import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CalendarEvent, CalendarEventTimesChangedEvent, CalendarView, DAYS_OF_WEEK } from 'angular-calendar';
import { endOfDay, isSameDay, isSameMonth, startOfDay } from 'date-fns';
import { Subject } from 'rxjs';
import IUpdate from 'src/app/interfaces/update';
import { UpdatesService } from 'src/app/services/updates.service';
import { environment, jiraUrl } from 'src/environments/environment';
import { UpdateComponent } from '../update/update.component';
import sampleData from '../../../assets/sampleData/updatesSample.json';


const colors: any = { red: { primary: '#ad2121', secondary: '#FAE3E3', }, blue: { primary: '#1e90ff', secondary: '#D1E8FF', }, yellow: { primary: '#e3bc08', secondary: '#FDF1BA', }, };

@Component({
  selector: 'calendar-component',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['calendar.component.scss'],
  templateUrl: 'calendar.component.html',
})
export class CalendarComponent {

  @Input() isModal = false; // pas utiliser?
  @Input() eventEditable = true;

  updates: IUpdate[] = [];
  refresh: Subject<any> = new Subject();
  events: CalendarEvent[] = [];
  view: CalendarView = CalendarView.Month;

  CalendarView = CalendarView;
  days = CalendarView.Day;

  viewDate: Date = new Date();


  // Affiche le "popup" des events sous le jour
  activeDayIsOpen: boolean = true;
  locale: string = 'fr';
  weekStartsOn: number = DAYS_OF_WEEK.MONDAY;
  weekendDays: number[] = [DAYS_OF_WEEK.FRIDAY, DAYS_OF_WEEK.SATURDAY];

  // ce qui permet d'envoyer le valeur au composant parent du calendar, dans update-modal par exemple
  // on emit un event :this.dayClickedEvent.emit(date); et on peut le rcuprer dans le parent comme a
  @Output() dayClickedEvent = new EventEmitter<Date>();

  constructor(private updatesService: UpdatesService, private modalService: NgbModal) { }


  ngOnInit(): void {
    if(environment.demo) {
      this.viewDate = new Date("2023-02-14");
     let updates = sampleData as unknown;
      this.createEvent(sampleData)

    } else {
      this.getUpdates();
    }
  }


  getUpdates(): void {
    if(!environment.demo) {
    this.events = [];
    this.updatesService
      .getUpdates()
      .subscribe((data: any) => (
        this.createEvent(data.updates)
      ));
    }
  }

  createEvent(updates: any[]) {
    let actions = this.eventEditable ? [
      {

        label: '<i class="fas fa-fw fa-pencil-alt fs-5 text-info"></i>',
        onClick: ({ event }: { event: CalendarEvent }): void => {
          this.handleEvent('editUpdate', event);
        }
      },
      {
        label: '<i class="fas fa-fw fa-trash-alt fs-5 text-danger"></i>',
        a11yLabel: 'Delete',
        onClick: ({ event }: { event: CalendarEvent }): void => {
          // this.events = this.events.filter((iEvent) => iEvent !== event);
          this.handleEvent('deleteUpdate', event);
        },
      }
    ] : [];

    for (let u of updates) {
      let event: CalendarEvent = {
        start: startOfDay(new Date(u.dueDate)),
        end: endOfDay(new Date(u.dueDate)),
        title: `${u.instance.target.toUpperCase()} - ${u.keyJira} - serveur ${u.instance.server.number.toUpperCase()}`,
        color: colors.red,
        cssClass: 'text-10',
        allDay: false,
        meta: { update: u },
        actions: actions
      }
      this.events.push(event)
    }
    this.refresh.next(CalendarComponent);
  };




  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }, event: any): void {


    this.dayClickedEvent.emit(date);

    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
      this.viewDate = date;
    }
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd,
  }: CalendarEventTimesChangedEvent): void {
    this.events = this.events.map((iEvent) => {
      if (iEvent === event) {
        return {
          ...event,
          start: newStart,
          end: newEnd,
        };
      }
      return iEvent;
    });
    this.handleEvent('goJira', event);
  }

  handleEvent(action: string, event: CalendarEvent): void {
    console.log(event.meta)
    switch (action) {
      case 'goJira':
        if(environment.demo) {
          window.open("http://www.florentplomb.ch",'_blank');
        }else {
        window.open(jiraUrl + 'browse/' + event.meta.update.keyJira, '_blank');
      }
        break;
      case 'editUpdate':
        const updateEditModal = this.modalService.open(UpdateComponent, {
          size: 'xl',
          animation: true,
        })

        updateEditModal.componentInstance.instance = event.meta.update.instance;
        updateEditModal.componentInstance.mode = "patch"

       updateEditModal.componentInstance.update = event.meta.update;

        updateEditModal.dismissed.subscribe(() => {
             
          this.getUpdates();
        })
        break;

        case 'deleteUpdate':
          const deleteUpdateModal = this.modalService.open(UpdateComponent, {
            size: 'xl',
            animation: true,
          })

          deleteUpdateModal.componentInstance.instance = event.meta.update.instance;

          deleteUpdateModal.componentInstance.mode = "delete"

         deleteUpdateModal.componentInstance.update = event.meta.update;

          deleteUpdateModal.dismissed.subscribe(() => {
            this.getUpdates();
          })
          break;



      default:
        break;
    }

  }

  addEvent(): void {
    this.events = [
      ...this.events,
      {
        title: 'New event',
        start: startOfDay(new Date()),
        end: endOfDay(new Date()),
        color: colors.red,
        draggable: true,
        resizable: {
          beforeStart: true,
          afterEnd: true,
        },
      },
    ];
  }

  deleteEvent(eventToDelete: CalendarEvent) {
    this.events = this.events.filter((event) => event !== eventToDelete);
  }

  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }
}
