import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { format } from 'date-fns';
import { fr } from 'date-fns/locale';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {

  public now:string = format(new Date(),"dd MMMM yyyy - HH' h 'mm' m 'ss' s",{ locale: fr });

  constructor(private router: Router) {

    setInterval(() => {
      this.now = format(new Date(),"dd MMMM yyyy - HH' h 'mm' m 'ss' s",{ locale: fr });
    }, 1000);

    this.inactivityTime();

  }


inactivityTime() {
    var time;
    window.onload = resetTimer;
    // DOM Events
    document.onmousemove = resetTimer;
    document.onkeydown = resetTimer;

    function logout() {
      alert("Session timeout (50min) - Page reloading")
        window.location.reload();
        //location.href = 'logout.html'
    }

    function resetTimer() {
        clearTimeout(time);
        time = setTimeout(logout, 3000000) // 50min
        // 1000 milliseconds = 1 second
    }
};


}
