import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import {
  NgbModal,
  NgbModalOptions
} from '@ng-bootstrap/ng-bootstrap';

import { environment, jiraUrl } from 'src/environments/environment';
import IProject from 'src/app/interfaces/project';
import IUser from 'src/app/interfaces/user';
import { AuthService } from 'src/app/services/authService.service';
import { ProjectsService } from 'src/app/services/projects.service';
import { CalendarComponent } from '../calendar/calendar.component';
import { ArrayUtilsService } from 'src/app/services/array-utils.service';
import { Router } from '@angular/router';
import sampleData from '../../../assets/sampleData/projectsSample.json';


@Component({
  selector: 'app-projects-list',
  templateUrl: './projects-list.component.html',
  styleUrls: ['./projects-list.component.scss'],
})
export class ProjectsListComponent implements OnInit {

  @ViewChild(CalendarComponent) childCalendarComp!: CalendarComponent;

  user!: IUser;
  projects: IProject[] = [];
  public searchText: any;
  public instanceCounter: number;
  public itemHeights:number[] = [];

  closeResult: string | undefined;
  modalOptions: NgbModalOptions | undefined;

  constructor(
    private projectsService: ProjectsService,
    private auth: AuthService,
    private router: Router,
    private arrayUtils: ArrayUtilsService
  ) {
    this.modalOptions = {
      backdrop: 'static',
      backdropClass: 'customBackdrop',
    };
  }


  ngOnInit(): void {
    if(environment.demo) {

    this.projects = sampleData as unknown as IProject[];
  
    this.computeHeights(),
    this.computeInstance()
     
  } else {
    this.auth.getSotrageUser().then((user) => {
      this.user = user;
    });


    this.getProjects();
  }


  }

  refreshData() {

    this.getProjects();
    this.childCalendarComp.getUpdates();

  }

  openProject(projectKey: string): void {
    let url = jiraUrl + 'projects/' + projectKey
    window.open(url, '_blank');
  }

  async getProjects(): Promise<void> {
    this.projectsService
      .getProjects(true)
      .subscribe((projects: IProject[]) => (
        this.projects = this.arrayUtils.sortAlpha(projects,"name"),
        this.computeHeights(),
        this.computeInstance()

      ));
  }

  computeHeights() {
    let base = 55;
  this.itemHeights = this.projects.map(p => {
     p.instances.length

     return base + (p.instances.length*32);
   })

  }


  back() {
    this.router.navigate(["home"]);
  }


  computeInstance() {
    this.instanceCounter = 0;
    for (let p of this.projects) {
      this.instanceCounter += p.instances.length;
    }


  }



}
