import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { CalendarEvent } from 'angular-calendar';
import { lastValueFrom, map, Observable, of, tap } from 'rxjs';
import { environment } from '../../environments/environment';
import IInstance from '../interfaces/instance';
import IProject from '../interfaces/project';
import IServer from '../interfaces/server';
import IUpdate from '../interfaces/update';
import { AuthService } from '../services/authService.service';

@Injectable({
  providedIn: 'root',
})
export class ProjectxApi {
  private path = environment.projectxApiEndpoint;
  // headers: {secret : this.auth.getUser()?.secret }}
  public secret!: any;

  constructor(public http: HttpClient, private auth: AuthService) {
    this.setSecret();
  }

  async setSecret() {
    let user = await this.auth.getSotrageUser();
    this.secret = user.secret;
  }

  getProjects(onlyWithInstances: boolean): Observable<IProject[]> {
    return this.http
      .get<IProject[]>(this.path + 'projects', {
        params: { onlyWithInstances: onlyWithInstances },
        headers: { secret: this.secret },
      })
      .pipe(
        tap((_) => console.log('fetched projects')),
        map((data) => data)
      );
  }

  getProjectsInstances(): Promise<any[]> {
    return lastValueFrom(
      this.http
        .get<any[]>(this.path + 'servers/instances', { headers: { secret: this.secret } })
        .pipe(
          tap((_) => console.log('fetched servers')),
          map((data) => data)
        )
    );
  }

  getServers(): Observable<IServer[]> {
    return this.http
      .get<IServer[]>(this.path + 'servers', { headers: { secret: this.secret } })
      .pipe(
        tap((_) => console.log('fetched servers')),
        map((data) => data)
      );
  }

  postServer(server: IServer): Observable<IServer> {
    return this.http
      .post<IServer>(this.path + 'servers', server, { headers: { secret: this.secret } })
      .pipe(
        tap((_) => console.log('post server')),
        map((data) => data)
      );
  }

  postInstance(instance: IInstance[]): Promise<IInstance> {
    return lastValueFrom(
      this.http
        .post<IInstance>(this.path + 'instances', instance, { headers: { secret: this.secret } })
        .pipe(
          tap((_) => console.log('post instance')),
          map((data) => data)
        )
    );
  }

  patchInstance(instance: IInstance): Promise<IInstance> {
    return lastValueFrom(
      this.http
        .patch<IInstance>(this.path + 'instances/' + instance.id, instance, {
          headers: { secret: this.secret },
        })
        .pipe(
          tap((_) => console.log('patch instance')),
          map((data) => data)
        )
    );
  }

  patchInstanceEmails(instance: IInstance, mails: string[]): Promise<IInstance> {
    return lastValueFrom(
      this.http
        .patch<IInstance>(this.path + 'instances/' + instance.id + '/mails', mails, {
          headers: { secret: this.secret },
        })
        .pipe(
          tap((_) => console.log('patch mail instance')),
          map((data) => data)
        )
    );
  }

  deleteInstance(instance: IInstance): Promise<IInstance> {
    return lastValueFrom(
      this.http
        .delete<IInstance>(this.path + 'instances/' + instance.id, {
          headers: { secret: this.secret },
        })
        .pipe(
          tap((_) => console.log('delete instance')),
          map((data) => data)
        )
    );
  }

  async patchUpdate(update: IUpdate, body: any): Promise<IUpdate> {
    return lastValueFrom(
      this.http
        .patch<IUpdate>(this.path + 'updates/' + update.id, body, {
          headers: { secret: this.secret },
        })
        .pipe(
          tap((_) => console.log('patch updates')),
          map((data) => data)
        )
    );
  }

  async postUpdate(update: IUpdate): Promise<IUpdate> {
    return lastValueFrom(
      this.http
        .post<IUpdate>(this.path + 'updates', update, { headers: { secret: this.secret } })
        .pipe(
          tap((_) => console.log('post updates')),
          map((data) => data)
        )
    );
  }

  async deleteUpdate(update: IUpdate): Promise<IUpdate> {
    return lastValueFrom(
      this.http
        .delete<IUpdate>(this.path + 'updates/' + update.id, { headers: { secret: this.secret } })
        .pipe(
          tap((_) => console.log('delete updates')),
          map((data) => data)
        )
    );
  }

  getUpdates(): Observable<IUpdate[]> {
    return this.http
      .get<IUpdate[]>(this.path + 'updates', { headers: { secret: this.secret } })
      .pipe(
        tap((_) => console.log('fetched updates')),
        map((data) => data)
      );
  }

  getNextsUpdates(instanceId): Observable<{
    lastUpdatesInstance: IUpdate[];
    updatesInstance: IUpdate[];
    updatesServer: IUpdate[];
  }> {
    // je sais pas comment passer proprement le queryparam
    // const opts = { params: new HttpParams({ fromString: `instanceId=${instanceId}` }) };
    return this.http
      .get<{
        lastUpdatesInstance: IUpdate[];
        updatesInstance: IUpdate[];
        updatesServer: IUpdate[];
      }>(this.path + `updates/nexts?instanceId=${instanceId}`, {
        headers: { secret: this.secret },
      })
      .pipe(
        tap((_) => console.log('fetched updates')),
        map((data) => data)
      );
  }
  getJiraProject(): Promise<any> {
    return lastValueFrom(
      this.http
        .get<any>(this.path + '/projects/utils/getJira', { headers: { secret: this.secret } })
        .pipe(
          tap((_) => console.log('fetched project')),
          map((data) => data)
        )
    );
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
