import { Pipe, PipeTransform } from '@angular/core';

import IProject from 'src/app/interfaces/project';
import IInstance from '../interfaces/instance';

@Pipe({ name: 'projectsFilterPipe' })
export class ProjectsFilterPipe implements PipeTransform {
  transform(allprojects: IProject[], searchStr: string) {

    if (searchStr && searchStr.trim() !== '') {
      searchStr = searchStr.toLowerCase();

      allprojects = allprojects.filter((project) => {


        for (let instance of project.instances) {

          if(instance.target?.toLowerCase().includes(searchStr) || instance.server?.number.toLowerCase().includes(searchStr)){
            return true;
          }

        }

        return false;


      });

    }
    return allprojects;
  }
}



@Pipe({ name: 'instancesFilterPipe' })
export class InstancesFilterPipe implements PipeTransform {
  transform(allInstances: IInstance[], searchStr: string) {


    if (searchStr && searchStr.trim() !== '') {
      allInstances = allInstances.filter((instance) => {
        return (instance.target?.toLowerCase().includes(searchStr) || instance.server?.name.toLowerCase().includes(searchStr))
      });

    }

    return allInstances;



  }
}



