import { NgModule, LOCALE_ID } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ProjectsListComponent } from './components/projects-list/projects-list.component';
import { Ng2SearchPipeModule } from 'ng2-search-filter';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { InstancesFilterPipe, ProjectsFilterPipe } from './pipes/filter.pipe';
import { CalendarComponent } from './components/calendar/calendar.component';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';
import localeFr from '@angular/common/locales/fr';
import { registerLocaleData } from '@angular/common';
import { LoginModule } from './components/login/login.module';
import { ToastrModule } from 'ngx-toastr';
import { UpdateComponent } from './components/update/update.component';
import { ManagerComponent } from './components/manager/manager.component';
import { NewServerComponent } from './components/new-server/new-server.component';
import { InstanceComponent } from './components/instance/instance.component';
import { SprintBoardComponent } from './components/sprint-board/sprint-board.component';
import { HomeComponent } from './components/home/home.component';
import { MatIconModule } from '@angular/material/icon';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { InstanceItemComponent } from './components/projects-list/instance-item/instance-item.component';
import {ScrollingModule} from '@angular/cdk/scrolling';
import { CustomVirtualScrollDirective } from './directives/custom-virtual-scroll-strategy.directive';
import { CallbackComponent } from './components/oauth/callback/callback.component';

registerLocaleData(localeFr);



@NgModule({
  declarations: [
    AppComponent,
    ProjectsListComponent,
    InstancesFilterPipe,
    ProjectsFilterPipe,
    CalendarComponent,
    UpdateComponent,
    ManagerComponent,
    NewServerComponent,
    InstanceComponent,
    SprintBoardComponent,
    HomeComponent,
    InstanceItemComponent,
    CustomVirtualScrollDirective,
    CallbackComponent,

    ],
  imports: [
    CalendarModule.forRoot({
      provide: DateAdapter,
      useFactory: adapterFactory,
    }),
    BrowserAnimationsModule,
    MatIconModule,
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
    Ng2SearchPipeModule,
    ReactiveFormsModule,
    ScrollingModule,
    FormsModule,
    LoginModule,
    ToastrModule.forRoot(),
    NgbModule
  ],
  providers: [
    { provide: LOCALE_ID, useValue: 'fr-FR' },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
