import { Injectable } from '@angular/core';
import IUser from '../interfaces/user';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  // Never store the password, but keepit in service durgin the session
  private user: IUser | undefined;


  constructor() { }

  async setUser(user: IUser) {
    // pas besoin d'await?
    this.user = user;
    localStorage.setItem('user', JSON.stringify(user));


  }

  async setToken(token: string) {
    // pas besoin d'await?
    this.user.token = token;
   

  }

  
  async getToken() {
    // pas besoin d'await?
   return this.user?.token || false
  // return "this.user.token"
   

  }


  async setUserSecret(secret: string) {
    let data = await localStorage.getItem('user');
    let user: IUser = JSON.parse(data);
    user.secret = secret;
    localStorage.setItem('user', JSON.stringify(user));

  }


  async getSotrageUser(): Promise<IUser> {

    // si on demande le mot de passe on prend le user en cache , on en sauve pas le pw dans le local storage

    let user = await localStorage.getItem('user') || '{}';

    let userReturn: IUser = JSON.parse(user);

    userReturn.pw = this.user?.pw;

    return userReturn;
  }
}
