import { transition } from '@angular/animations';
import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { endOfDay, format, formatISO, isAfter, isBefore, isSameDay, startOfDay } from 'date-fns';
import { fr } from 'date-fns/locale';
import { Issue } from 'jira.js/out/agile/models';
import { Sprint } from 'jira.js/out/agile/models/sprint';
import { ToastrService } from 'ngx-toastr';
import { forkJoin, Observable, of } from 'rxjs';
import { JiraApi } from 'src/app/api/jiraApi';
import { ProjectxApi } from 'src/app/api/projectxApi';
import IInstance from 'src/app/interfaces/instance';
import { IIssue } from 'src/app/interfaces/jira';
import IProject from 'src/app/interfaces/project';
import IUpdate from 'src/app/interfaces/update';
import { AuthService } from 'src/app/services/authService.service';
import { DateService } from 'src/app/services/date.service';
import { environment, jiraUrl } from 'src/environments/environment';

@Component({
  selector: 'updateModal',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.scss'],
})
export class UpdateComponent implements OnInit {
  @Input()
  public mode: 'create' | 'patch' | 'delete' = 'create';

  // Mode create:
  @Input() public project: IProject = {
    instances: [],
    jiraId: '',
    key: '',
    name: '',
    _id: '',
  };
  @Input()
  public instance!: IInstance;

  // Mode: patch, update alreadyExist
  public update: IUpdate;

  public nextsUpdates: {
    lastUpdatesInstance: IUpdate[];
    updatesInstance: IUpdate[];
    updatesServer: IUpdate[];
  } = {
    lastUpdatesInstance: [],
    updatesInstance: [],
    updatesServer: [],
  };

  public sprint: Sprint = { id: 0, state: '', name: '' };
  public sprints: Sprint[] = [];
  public composants: any[] = [];
  public dateSelected: Date;

  constructor(
    private fb: FormBuilder,
    private toastr: ToastrService,
    private jiraApi: JiraApi,
    private projectxApi: ProjectxApi,
    private activeModal: NgbModal,
    private auth: AuthService,
    private dateService: DateService
  ) {}

  public updateCreateForm = this.fb.group({
    date: ['', Validators.required],
    instance: ['', Validators.required],
    composant: ['', Validators.required],
    project: ['', Validators.required],
  });

  ngOnInit(): void {
    this.toastr.toastrConfig.disableTimeOut = true;

    this.dateSelected = this.update?.dueDate ? new Date(this.update?.dueDate) : new Date();
    console.log(this.mode);
    this.init();
  }

  async init() {
    if(environment.demo) {
      this.sprint = { id: 0, state: '', name: "Sprint F - February" };
    } 
    else {

      await this.projectxApi
      .getNextsUpdates(this.instance.id)
      .subscribe(
        (data: {
          lastUpdatesInstance: IUpdate[];
          updatesInstance: IUpdate[];
          updatesServer: IUpdate[];
        }) => {
          console.log(data);
          this.dateService.sortByDate(data.updatesInstance, 'dueDate');
          this.dateService.sortByDate(data.updatesServer, 'dueDate');
          this.dateService.sortByDate(data.lastUpdatesInstance, 'dueDate');
          this.nextsUpdates.updatesInstance = data.updatesInstance;
          this.nextsUpdates.updatesServer = data.updatesServer;
          this.nextsUpdates.lastUpdatesInstance = data.lastUpdatesInstance.reverse();
        }
        );
      }
        
    this.updateCreateForm.controls['date'].setValue(
      format(new Date(this.dateSelected), 'dd MMM yyyy', { locale: fr })
    );
    this.updateCreateForm.controls['instance'].setValue(this.instance?.target);
    this.updateCreateForm.controls['project'].setValue(this.project.name);
    let project;
    let sprints ;
    if(!environment.demo) {
    if (this.project.jiraId) {
      project = await this.jiraApi.getProject(this.project.key);
    }
    sprints = await this.jiraApi.getSprints();
  }

    if (project?.components?.length > 0) {
      this.composants = project.components.map(function (item: any) {
        return { name: item['name'], id: item['id'] };
      });
    } else {
      this.composants.push({ name: 'Pas de composant', id: 0 });
    }

    if (sprints?.values) {
      this.sprints = sprints.values;

      this.sprint = this.getSprint(this.sprints);
    }
  }

  async onSubmit() {
    switch (this.mode) {
      case 'create':
        await this.createUpdate();
        break;

      case 'patch':
        await this.patchUpdate();
        break;

      case 'delete':
        await this.deleteUpdate();
        break;

      default:
        break;
    }
  }

  private getSprint(sprints: Sprint[]): Sprint {
    let sprintSelected!: Sprint;

    for (let sprint of sprints) {
      // TO DO UTILISER .aftter and .before plutot que compareAsc
      //  return 1 if the first date is after the second, -1 if the first date is before

      let dateSel = startOfDay(this.dateSelected);
      let spStart = startOfDay(new Date(sprint.startDate || ''));
      let spEnd = endOfDay(new Date(sprint.endDate || ''));

      if (
        (isSameDay(dateSel, spStart) || isAfter(dateSel, spStart)) &&
        (isSameDay(dateSel, spEnd) || isBefore(dateSel, spEnd)) &&
        sprint.name.includes('Robusta')
      ) {
        return (sprintSelected = sprint);
      }
    }
    return sprintSelected;
  }

  // Fonction sur le composant enfant "<calendar-component>"" qui rcupre la valeur emise par ce child composant.
  dayClickedEventHander(date: Date) {
    this.dateSelected = date;
    if(!environment.demo) {
      this.sprint = this.getSprint(this.sprints);
    } 

    this.updateCreateForm.controls['date'].setValue(format(date, 'dd MMM yyyy', { locale: fr }));
  }

  async createUpdate() {
    let issueFields: IIssue = {
      project: { key: this.project?.key },
      summary: `M.A.J - ${this.instance?.target} - s${this.instance?.server.number} - ${this.updateCreateForm.value.date}`,
      description: `Mise  jour gnre par ProjectX - URL :${this.instance.url}`,
      components: [
        {
          id:
            this.updateCreateForm.value?.composant?.toString() === 0
              ? null
              : this.updateCreateForm.value.composant.toString(),
        },
      ],
      customfield_10020: this.sprint.id,
      duedate: endOfDay(this.dateSelected),
      issuetype: {
        name: this.instance?.type === 'PROD' ? 'Mise  jour App PROD' : 'Mise  jour DEMO',
      },
      assignee: null,
    };

    let preIssue = { fields: issueFields };

    let issue: Issue = await this.jiraApi.postIssue(preIssue).catch(() => {
      throw this.handleError('Jira Failure ', "Vrifiez l'tat de votre ticket JIRA");
    });

    this.openIssue(issue.key);

    let preUpdate: IUpdate = {
      dueDate: this.dateSelected,
      url: this.instance.url,
      instance: this.instance,
      keyJira: issue.key,
    };

    await this.projectxApi.postUpdate(preUpdate).catch(() => {
      throw this.handleError('Post projectX Update' + this.update.keyJira);
    });

    // refresh le calendar
    this.activeModal.dismissAll();
  }

  async patchUpdate(): Promise<void> {
    await this.projectxApi.patchUpdate(this.update, { dueDate: this.dateSelected }).catch(() => {
      throw this.handleError('ProjectX : Update failed' + this.update.keyJira);
    });

    await await this.jiraApi
      .putIssue(this.update.keyJira, {
        fields: {
          summary: `M.A.J - ${this.instance?.target} - s${this.instance?.server.number} - ${this.updateCreateForm.value.date}`,
          duedate: endOfDay(this.dateSelected),
          customfield_10020: this.sprint.id,
        },
      })
      .catch(() => {
        throw this.handleError(
          'Erreur ',
          "Vrifiez l'tat de votre ticket JIRA et contactez l'administateur" + this.update.keyJira
        );
      });

    this.activeModal.dismissAll();
  }

  async deleteUpdate(): Promise<void> {
    await this.projectxApi.deleteUpdate(this.update).catch(() => {
      throw this.handleError('ProjectX : Update failed ' + this.update.keyJira);
    });

    await await this.jiraApi
      .putIssue(this.update.keyJira, { fields: { duedate: null, summary: 'M.A.J annule' } })
      .catch(() => {
        throw this.handleError(
          'Jira Failure ',
          "Vrifiez l'tat de votre ticket JIRA:" + this.update.keyJira
        );
      });
    this.toastr.warning("Vrifiez l'tat de votre ticket JIRA: " + this.update.keyJira, null, {
      timeOut: 8000,
    });

    // let transistion = await this.jiraApi.getIssueTransition(this.update.keyJira).catch(() => {
    //   throw this.handleError("ProjectX : Update failed")
    // });

    // transistion.transitions.find(e => e.name.includes)
    // '291' : '241' Annul Probablement rsolu
    // await await this.jiraApi.postIssueTransition(this.update.keyJira, transitionId).catch(() => {
    //   throw this.handleError("JIra: Update issue failed");
    // });;

    this.activeModal.dismissAll();
  }

  openIssue(key: string) {
    window.open(jiraUrl + 'browse/' + key, '_blank');
  }

  private handleError<T>(message, title = 'Failure', result?: T) {
    this.toastr.error(title, message);
    this.activeModal.dismissAll();
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${message} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
