import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ArrayUtilsService {

  constructor() { }

  sortAlpha(array, props) {

    return array = array.sort((a, b) => {
      let fa = a[props]?.trim().toLowerCase(),
        fb = b[props]?.trim().toLowerCase();
      if (fa < fb) {
        return -1;
      }
      if (fa > fb) {
        return 1;
      }
      return 0;
    })
  }

  sortNumeric(array, props) {

    return array = array.sort((a, b) => {
      let fa = parseInt(a[props]),
        fb = parseInt(b[props]);
      if (fa < fb) {
        return -1;
      }
      if (fa > fb) {
        return 1;
      }
      return 0;
    })
  }
}
