import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import IUpdate from '../interfaces/update';
import { ProjectxApi } from '../api/projectxApi';

@Injectable({
  providedIn: 'root'
})
export class UpdatesService {

  constructor(private api: ProjectxApi) { }


  getUpdates(): Observable<IUpdate[]> {
    return this.api.getUpdates();
  }

}
