import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { environment } from 'src/environments/environment';
import { AuthService } from '../services/authService.service';

@Injectable({
  providedIn: 'root'
})
export class isLoginGuard implements CanActivate {

  constructor(private authService: AuthService, private router: Router) {

  }
  async canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

  
  let userToken = await this.authService?.getToken() || false;


 // c'est moche vu que le token je le sauve pas dans le local storage, si on refresh une page forcement le token existe plus mais pas plus mal pour v1
    if(userToken || environment.demo){
      return true;
    } else{
      this.router.navigate(['login']);
      return false;
    }

  }
}
