import { Component, OnInit } from '@angular/core';
import { saveAs } from 'file-saver';
import { Sprint } from 'jira.js/out/agile/models';
import { JiraApi } from 'src/app/api/jiraApi';
import { DateService } from 'src/app/services/date.service';
import { jiraUrl } from 'src/environments/environment';
import { TicketTab } from './TicketTab.interface';
import { Router } from '@angular/router';
import { Project, Component as Composant } from 'jira.js/out/version2/models';

@Component({
  selector: 'app-sprint-board',
  templateUrl: './sprint-board.component.html',
  styleUrls: ['./sprint-board.component.scss'],
})
export class SprintBoardComponent implements OnInit {
  public spReporter: { name: string; sp: number }[] = [];
  public spPerAC: { name: string; sp: number; issues: any[] }[] = [];
  public spProject: {
    name: string;
    sp: number;
    tdev: number;
    ttest: number;
    treview: number;
    tdevtot: number;
    issues: { key: string; sp: number }[];
    issueList: string;
  }[] = [];
  public majProject: { project: string; total: number }[] = [];
  public issuesType: { type: string; sp: number; devTot: number }[] = [];
  public sprints: Sprint[] = [];
  public sprintSelected: any;
  public useOwnJql: boolean = false;

  public jqlSPbyProjectAC: string;

  public loadProjects: boolean;

  public tabs: TicketTab[];

  constructor(private jiraApi: JiraApi, private router: Router, private dateService: DateService) {}

  ngOnInit(): void {
    this.init();
  }

  async init() {
    let data = await this.jiraApi.getSprints();

    if (data.values) {
      this.sprints = data.values;
    }

    let index = this.dateService.getActiveSprint(this.sprints);
    this.dateService.sortByDate(this.sprints, 'startDate');

    this.sprintSelected = this.sprints[index];

    this.tabs = [
      {
        name: 'SPbyProjectAC',
        jql: `AND project in (ACFIN, ACGEN, ACCOM, ACPROD, ACPRO, ACIT, ACSAL, ACTIM, AC, AUB, AUUI, "AC Gestion Projet") AND issuetype in (Amlioration, Bug, Estimation, "Incident ERP", "Incident IT", "Nouvelle fonction", "Nouvelle installation", Story, "Rcit/Sance", Support,  Tche, "Tche IT", "Tche client", pope) AND component not in (ScrumMaster, "Rserve globale du Sprint", Mentoring, "Sances d'estimation", "Sance Interne")`,
      },
    ];

    this.onSprintChange();
  }

  onSprintChange() {
    this.spPerReporter(this.sprintSelected);
    this.spPerProject(this.sprintSelected);
    this.majPerProject(this.sprintSelected);
    this.issuePerType(this.sprintSelected);
    this.spPerAmeliorationContinue(this.sprintSelected);
  }

  async spPerReporter(sprint: Sprint) {
    let jql = `sprint=${sprint.id} AND component not in ("Rserve globale du Sprint") AND status != Cltur AND status != Annul AND issuetype not in ("Mise  jour App PROD", "Mise  jour DEMO")`;

    let issues = await this.jiraApi.searchJQL(jql);

    let spPerReporter: { name: string; sp: number }[] = [];

    for (let i of issues) {
      let obj = spPerReporter.find((e) => e.name == i.fields.reporter.name);

      if (!obj) {
        spPerReporter.push({
          name: i.fields.reporter.name,
          sp: i.fields.customfield_10031,
        });
      } else {
        obj.sp += i.fields.customfield_10031;
      }
    }

    this.spReporter = spPerReporter.sort((a, b) => b.sp - a.sp);

    this.spReporter = this.spReporter.filter((r) => {
      return r.sp != null;
    });
  }

  async spPerProject(sprint: Sprint) {
    let jql = `sprint=${sprint.id} AND  status != Annul`;

    let issues = await this.jiraApi.searchJQL(jql);

    let spPerProject: {
      name: string;
      sp: number;
      tdev: number;
      ttest: number;
      treview: number;
      tdevtot: number;
      issues: { key: string; sp: number }[];
      issueList: string;
    }[] = [];

    console.log(issues)

    for (let i of issues) {
      let obj = spPerProject.find((e) => e.name == i.fields.project.name);

      let tdevtot =
        i.fields.customfield_10084 + i.fields.customfield_10087 + i.fields.customfield_10091;

      if (!obj) {
        spPerProject.push({
          name: i.fields.project.name,
          sp: i.fields.customfield_10031,
          tdev: i.fields.customfield_10084,
          ttest: i.fields.customfield_10087,
          treview: i.fields.customfield_10091,
          tdevtot: tdevtot,
          issues: [{ key: i.key, sp: i.fields.customfield_10031 }],
          issueList: '',
        });
      } else {
        obj.sp += i.fields.customfield_10031;
        obj.tdevtot += tdevtot;

        obj.issues.push({ key: i.key, sp: i.fields.customfield_10031 });
      }
    }
    spPerProject = spPerProject
      .filter((e) => {
        let keep = e.sp > 0;
        e.sp = Math.round(e.sp * 100) / 100;
        return keep;
      })
      .sort((a, b) => b.sp - a.sp);

    for (let e of spPerProject) {
      let str = '';

      for (let i of e.issues.sort((a, b) => b.sp - a.sp)) {
        str += `<a target="_blank" href="${jiraUrl}browse/${i.key}">${i.key}</a>  (${
          i.sp ? i.sp : 0
        }sp),   `;
      }
      str += `<span class="fw-bold">(#${e.issues.length})<span>`;

      e.issueList = str;
    }

    let total = spPerProject.reduce((agg, e) => {
      return agg + e.sp;
    }, 0);
    let totaldev = spPerProject.reduce((agg, e) => {
      return agg + e.tdevtot;
    }, 0);

    spPerProject.unshift({
      name: 'Total',
      sp: total,
      tdev: 0,
      ttest: 0,
      treview: 0,
      tdevtot: totaldev,
      issues: [],
      issueList: '',
    });
    this.spProject = spPerProject;
  }

  async majPerProject(sprint: Sprint) {
    let jql = `issuetype in ("Mise  jour App PROD", "Mise  jour DEMO") AND due > "${sprint.startDate?.substring(
      0,
      sprint.startDate.indexOf('T')
    )}" AND due < "${sprint.endDate?.substring(0, sprint.endDate.indexOf('T'))}"`;

    let issues = await this.jiraApi.searchJQL(jql);

    let majPerProject: { project: string; total: number }[] = [];

    for (let i of issues) {
      let obj = majPerProject.find((e) => e.project == i.fields.project.name);

      if (!obj) {
        majPerProject.push({ project: i.fields.project.name, total: 1 });
      } else {
        obj.total++;
      }
    }

    this.majProject = majPerProject.sort((a, b) => b.total - a.total);
    if (this.majProject.length > 0) {
      let sum = this.majProject.reduce((a, b) => ({
        project: '',
        total: a.total + b.total,
      }));

      this.majProject.unshift({
        project: 'Total (' + (sum.total / 8).toFixed(1) + ' / jour)',
        total: sum.total,
      });
    }
  }

  async issuePerType(sprint: Sprint) {
    // let jql = `sprint=${sprint.id} AND summary !~ "Dductions*" AND status != Cltur AND status != Annul AND issuetype not in ("Mise  jour App PROD", "Mise  jour DEMO")`
    let jql = `sprint=${sprint.id} AND  status != Annul `;
    // 8 and &maxResults=1500
    //`sprint=${params.sprintId} AND ${params.more}`

    let issues = await this.jiraApi.searchJQL(jql);

    let issuePerType: { type: string; sp: number; devTot: number }[] = [];

    for (let i of issues) {
      let obj = issuePerType.find((e) => e.type == i.fields.issuetype.name);

      // "tdev": i.fields.customfield_10084, "ttest": i.fields.customfield_10087, "treview": i.fields.customfield_10091
      if (!obj) {
        issuePerType.push({
          type: i.fields.issuetype.name,
          sp: i.fields.customfield_10031,
          devTot:
            i.fields.customfield_10084 + i.fields.customfield_10087 + i.fields.customfield_10091,
        });
      } else {
        obj.devTot +=
          i.fields.customfield_10084 + i.fields.customfield_10087 + i.fields.customfield_10091;
        obj.sp += i.fields.customfield_10031;
      }
    }

    this.issuesType = issuePerType.sort((a, b) => b.devTot - a.devTot);

    let sum = this.issuesType.reduce((a, b) => ({
      type: '',
      sp: a.sp + b.sp,
      devTot: a.devTot + b.devTot,
    }));

    this.issuesType.unshift({ type: 'Total', sp: sum.sp, devTot: sum.devTot });

    this.issuesType = this.issuesType.filter((r) => {
      return r.sp != null;
    });
  }

  // AC = amlioration continue
  async spPerAmeliorationContinue(sprint: Sprint) {
    let tab = this.tabs.find((e) => {
      return e.name === 'SPbyProjectAC';
    });
    let jql;

    if (this.useOwnJql) {
      jql = `${this.jqlSPbyProjectAC}`;
    } else {
      this.jqlSPbyProjectAC = `sprint=${this.sprintSelected?.id} ${tab.jql}`;
      jql = this.jqlSPbyProjectAC;
    }

    let issues = await this.jiraApi.searchJQL(jql);

    let spPerAC: { name: string; sp: number; issues: any[] }[] = [];

    for (let i of issues) {
      let obj = spPerAC.find((e) => e.name == i.fields.project.name);

      if (!obj) {
        spPerAC.push({
          name: i.fields.project.name,
          sp: i.fields.customfield_10031,
          issues: [i],
        });
      } else {
        obj.sp += i.fields.customfield_10031;
        obj.issues.push(i);
      }
    }
    for (let i of spPerAC) {
      i.issues.sort((a, b) => b.fields.customfield_10031 - a.fields.customfield_10031);
    }

    this.spPerAC = spPerAC;

    let sum;
    if (this.spPerAC.length >= 1) {
      sum = this.spPerAC.reduce((a, b) => ({
        name: '',
        sp: a.sp + b.sp,
        issues: [],
      }));
    }

    this.spPerAC.unshift({ name: 'Total', sp: sum?.sp, issues: [] });
  }

  async componentsByProject() {
    if (
      confirm('Voulez-vous vraiment lancer la tlchargement, cela peut prendre environ 3min ?')
    ) {
      this.loadProjects = true;

      let projects: Project[] = await this.jiraApi.getProjects();

      let torage: (Project | Composant)[] = projects;
      let promise: Promise<any> = Promise.resolve('start');
      let queue = [];

      let result = [];

      let m = {};
      let m2: any = {};
      let i = 0;

      while (torage.length) {
        if (queue.length > 50) {
          let q = queue;
          promise = promise.then(() => {
            return Promise.all(q);
          });
          queue = [];
        }
        await promise;

        let f = torage.shift();

        if ((f as Project).key) {
          queue.push(
            this.jiraApi.getProject(f.id).then((project) => {
              project.components.forEach((c) => {
                m[c.id] = project.id;
                m2[project.id] = {
                  project: project.name,
                  log: [],
                };
                torage.push(c);
              });

              return 'P' + project.id;
            })
          );
        } else {
          queue.push(
            this.jiraApi
              .getComponent(f.id)
              .then((composant) => {
                let pId = m[composant.id];
                let r = m2[pId];
                let s: string = ' asd 123 ';
                s.trim();
                result.push(`${r.project?.trim()};${composant?.name.trim()};${composant.archived}`);

                return 'C' + composant.id;
              })
              .catch((e) => {
                let pId = m[f.id];
                let r = m2[pId];
                r.log.push('error ' + f.id);
              })
          );
        }
      }

      result.unshift('Projet;Composant;Archive');

      let csvArray = result.join('\r\n');
      var blob = new Blob([csvArray], { type: 'text/csv' });
      this.loadProjects = false;
      saveAs(blob, 'Projets-Composants.csv');
    }
  }

  back() {
    this.router.navigate(['home']);
  }

  openIssue(key: string) {
    window.open(jiraUrl + 'browse/' + key, '_blank');
  }
}
