import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { environment } from 'src/environments/environment';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { ManagerComponent } from './components/manager/manager.component';
import { CallbackComponent } from './components/oauth/callback/callback.component';
import { ProjectsListComponent } from './components/projects-list/projects-list.component';
import { SprintBoardComponent } from './components/sprint-board/sprint-board.component';
import { isLoginGuard } from './guards/isLoginGuard';
import { IsPermission } from './guards/isPermissionGuard';

const routes: Routes = [
  {
    path: 'home',
    component: HomeComponent,    
    canActivate: [isLoginGuard],
  },

  {
    path: 'sprints',
    component: SprintBoardComponent,
    canActivate: [isLoginGuard],
  },
  {
    path: 'projects',
    component: ProjectsListComponent,
    canActivate: [isLoginGuard],
 
  },
  {
    path: 'manager',
    component: ManagerComponent,
    canActivate: [isLoginGuard],

  },
  {
    path: 'callback',
    component: CallbackComponent,
  },
  {
    path: '',
    component: LoginComponent
  },
  {
    path: '**',
    component: LoginComponent,
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
